@import "main-bx";

.intro {
    &__inner {
        background-image: url(../img/content/bg.jpg);
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        width: 100%;
        height: 100%;

        @include below(769px) {
            background-image: url(../img/content/mbg.jpg);
            height: 375px;
            margin-bottom: 280px;
        }

        @include below(351px) {
            margin-bottom: 300px;
        }
    }

    &__top {
        padding: 130px 0;
        @include below(769px) {
            padding: 180px 0 30px;
        }
        
    }

    &__bottom {
        border-top: 1px solid rgba(255, 255, 255, 0.2);
        padding: 30px 0;

        @include below(769px) {
            padding: 40px 0 0;
        }

        @include below(351px) {
            padding: 20px 0 0;
        }
    }

    &__title {
        font-weight: normal;
        font-size: 50px;
        line-height: 110%;
        color: $white;
        margin-bottom: 32px;

        @include below(769px) {
            font-size: 25px;
        }
    }

    &__text {
        font-size: 30px;
        line-height: 140%;
        color: #E2DAD8;
        @include below(769px) {
            font-size: 18px;
        }
        &-num {
            color: $color_orange;
        }
    }
}