@import "main-bx";

.success {
    display: none;
    
    width: 100%;
    text-align: center;
    &.fancybox-content {
        width: 100%;
        max-width: 360px;
        padding: 70px 20px 50px;
        box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.3);
        border-radius: 5px;
    }

    .fancybox-button.fancybox-close-small {
        top: 10px;
        right: 10px;
    }

    &__inner {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    
    &__title {
        font-weight: 600;
        font-size: 24px;
        line-height: 140%;
        margin-bottom: 40px;
    }

    &__text {
        font-size: 16px;
        line-height: 140%;
        margin-bottom: 40px;
    }

    &__btn {
        padding: 16px 52px;
        border-radius: 30px;
    }
}