@import "main-bx";

.fixed {
    &__wrap {
        display: flex;
        justify-content: space-between;
        align-items: center;

        @include below(769px) {
            flex-direction: column;

            align-items: flex-start;
        }
    }

    &__title {
        font-weight: 600;
        font-size: 20px;
        line-height: 120%;
        color: $white;

        @include below(769px) {
            display: block;
            font-size: 18px;
            margin-bottom: 25px;
        }
    }

    &__payment {
        display: flex;
        justify-content: space-between;
        align-items: center;
        @include below(769px) {
            flex-direction: column;
            align-items: flex-start;
        }
        &-item {
            display: flex;
            justify-content: space-between;
            align-items: center;

            padding: 0 60px 0 25px;
            border-left: 1px solid rgba(255, 255, 255, 0.2);

            @include below(769px) {
                padding: 0;
                border: 0;
                
            }

            &:not(:last-child) {
                @include below(769px) {
                    margin-bottom: 16px;
                }
            }
        }

        &-icon {
            width: 100%;
            max-width: 50px;
            svg {
                width: 100%;
                height: 50px;
            }
            margin-right: 16px;
        }

        &-text {
            font-size: 16px;
            line-height: 120%;
            color: $white;
        }
    }
}