@import "main-bx";

.map {
    &__inner {
        position: relative;

        @include below(769px) {
            background: #292929;
            overflow: hidden;
        }
    }

    &__view {
        width: 100%;
        height: 600px;

        @include below(769px) {
            height: 330px;
        }
    }

    &__contact {
        position: absolute;
        top: 0;
        left: 360px;
        width: 100%;
        max-width: 250px;
        height: 100%;
        padding: 17px 0;

        @include below(1650px) {
            left: 220px;
        }

        @include below(1430px) {
            left: 100px;
        }

        @include below(1200px)  {
            left: 0;
        }

        @include below(769px) {
            position: static;
            z-index: 10000;
            top: auto;
            left: auto;
            padding: 0;
            max-width: 100%;
            border-top-left-radius: 5px;
            border-top-right-radius: 5px;
            transform: translateY(-10px);
        }

        &-inner {
            background: #292929;
            box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.2);
            border-radius: 5px;
            width: 100%;
            height: 100%;
            padding: 25px 0 25px 10px;

            @include below(769px) {
                padding: 15px;
            }
        }

        &-item {
            position: relative;
            cursor: pointer;
            padding: 20px 27px 20px 16px;


            border-top-left-radius: 5px;
            border-bottom-left-radius: 5px;
            transition: all .2s linear;
            &:hover,
            &.active {
                background-color: rgba(255, 255, 255, 0.1);

                .map__contact-pin {
                    right: -40px;
                    opacity: 1;
                }
            }

            @include below(769px) {
                display: flex;
                padding: 2px;
            }
        }

        &-pin {
            position: absolute;
            top: 0;
            right: 0;
            width: 40px;
            height: 100%;
            z-index: 100;
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: $color_orange;
            border-top-right-radius: 5px;
            border-bottom-right-radius: 5px;
            opacity: 0;
            transition: right .3s linear, opacity .2s linear;
            svg {
                width: 14px;
                height: 18px;                
            }

            @include below(769px) {
                display: none;
            }
        }
    }

    &__pin {
        display: flex;
        align-items: center;
        margin-bottom: 15px;
        &-icon {
            width: 10px;
            height: 10px;
            margin-right: 3px;
        }

        &-text {
            font-size: 12px;
            line-height: 100%;
            color: #9D9390;
        }
    }

    &__address {
        font-weight: 600;
        font-size: 12px;
        line-height: 100%;
        color: $white;
        margin-bottom: 5px;

        @include below(769px) {
            border-radius: 5px;
            background-color: rgba(59, 59, 59, 0.3);
            width: 140px;
            height: 70px;
            display: flex;
            align-items: center;
            padding-left: 10px;
            margin-right: 12px;
        }
    }

    &__phone {
        font-size: 20px;
        line-height: 100%;
        color: #E2DAD8;
        transition: all .2s linear;

        &:hover {
            color: $color_orange;
        }

        @include below(769px) {
            font-size: 18px;
        }
    }
}