@import "main-bx";

.tabs {
    width: 100%;
    max-width: 974px;
    margin: 0 auto;

    display: flex;
    justify-content: space-between;
    @include below(769px) {
        flex-direction: column;
        justify-content: flex-start;
    }

    &__title {
        font-weight: 600;
        font-size: 20px;
        line-height: 140%;
        color: #9D9390;
        margin-bottom: 10px;
    }

    &__item {
        cursor: pointer;

        display: flex;
        align-items: center;

        &.active {
            .tabs__text {
                color: $white;
                border-color: #F2742C;

                @include below(769px) {
                    border-color: transparent;
                    background-color: rgba(255, 255, 255, 0.2);
                }
            }

            @include below(769px) {
                border-color:#F2742C;
            }
        }

        @include below(769px) {
            display: flex;
            align-items: center;
            justify-content: space-between;
            border-radius: 5px;
            border-left: 5px solid rgba(255, 255, 255, 0.2);
        }

        &:not(:last-child) {
            @include below(769px) {
                margin-bottom: 10px;
            }
        }
    }


    &__text {
        font-weight: 600;
        font-size: 20px;
        line-height: 140%;
        color: $color_grey;
        transition: all .2s linear;
        display: inline-block;
        border-bottom: 1px solid transparent;

        @include below(769px) {
            font-size: 18px;
            padding: 10px;
            border-top-right-radius: 5px;
            border-bottom-right-radius: 5px;
            flex: 1;
        }
    }

    &__info {
        margin-bottom: -5px;
        margin-left: 12px;

        @include below(769px) {
            padding: 14px;
            border-radius: 5px;
            background-color: rgba(0, 0, 0, 0.5);
            margin-left: 6px;
            margin-bottom: 0;
        }

        svg {
            width: 20px;
            height: 20px;
            fill: #9D9390;
            transition: all .2s linear;

            @include below(769px) {
                width: 16px;
                height: 16px;
            }
        }
    }


    &__exp {
        position: relative;

        &:hover {
            .tabs__popup {
                z-index: 100;
                opacity: 1;
                transition: all .25s linear;
            }

            .tabs__info svg {
                fill: #F2742C;
            }
        }
    }

    &__popup {
        position: absolute;
        bottom: 40px;
        left: -158px;
        width: 360px;
        box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.3);
        border-radius: 5px;
        background-color: $white;
        padding: 30px;
        z-index: -1;
        opacity: 0;

        transition: all .25s linear .5s;

        @include below(769px) {
            bottom: 70px;
            left: auto;
            right: 0;
        }
    }

    &__list {
        position: relative;
    }

    &__figure {
        position: absolute;
        left: 0;
        right: 0;
        bottom: -48px;
        margin: 0 auto;
        width: 40px;
        height: 20px;

        @include below(769px) {
            margin: 0;
            left: auto;
            right: -28px;
        }
    }

    &__services {
        li {
            font-size: 14px;
            line-height: 120%;
        }
    }
}