@import "main-bx";
body {

    .select2-container--default .select2-selection--single .select2-selection__arrow {
        top: 7px;
        right: 20px;
    }

    .select2-container--default .select2-selection--single .select2-selection__arrow b {
        display: block;
        width: 12px;
        height: 12px;
        border-top: 2px solid #9D9390;
        border-left: 2px solid #9D9390;
        transform: rotate(225deg);
    }

    .select2-container--default.select2-container--open .select2-selection--single .select2-selection__arrow b {
        border-color: transparent;
        width: 12px;
        height: 12px;
        border-top: 2px solid #9D9390;
        border-left: 2px solid #9D9390;
        transform: rotate(45deg);
        border-width: 2px;
        margin-top: 5px;
    }

    .select2-search--dropdown {
        display: none;
    }
    
    .select2-results__options {
        padding: 0 12px;
    }
    
    .select2-results__option {
        padding: 14px 0;
    }
    
    .select2-container--default .select2-results__option--selected {
        background: transparent;
        color: #F2742C;
        position: relative;
        display: flex;
        justify-content: space-between;
        &::after {
            content: "";
            display: block;
            width: 6px;
            height: 10px;
            border: solid #000;
            border-width: 0 2px 2px 0;
            border-color: #F2742C;
            transform: rotate(45deg);
        }
    }
    .select2-results__option.select2-results__option--selectable.select2-results__option--selected.select2-results__option--highlighted{
        color: #F2742C;
    }

    
    .select2-container--default .select2-results__option--highlighted.select2-results__option--selectable {
        background-color: transparent;
        color: #000;
        opacity: 0.7;
    }
    
    .select2-container--open .select2-dropdown--below {
        background: #FFFFFF;
        border: 1px solid rgba(157, 147, 144, 0.3);
        box-sizing: border-box;
        box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.15);
        border-radius: 10px;
    }
}