@import "main-bx";

.form {

    fieldset {
        padding: 0;
        margin: 0;
        border: 0;
    }

    label {
        
        position: relative;
        display: block;
    }

    &__error {
        @include fontProperty($accent_font, 400, 12px, 1, $color_red);
        position: absolute;
        left: 0;
        right: 0;
        bottom: -20px;
        opacity: 0;
        text-align: center;
    }

    &__label {
        border: 1px solid #C8CACB;
        border-radius: 30px;
        margin-bottom: 30px;

        &.error {
            border-color: $color_red
        }

        &.error .form__error {
            opacity: 1;
        }

        &:focus {
            border: 1px solid #9D9390;
        }
    }

    .select2-container {
        width: 100% !important;
        padding: 15px 26px;
    }
    
    .select2-container--default .select2-selection--single {
        border: 0;
    }
    

    &__input {
        @include fontProperty($accent_font, 400, 16px, 23px, $black);
        text-align: center;
        width: 100%;
        padding: 15px 13px;
    }

    &__btn {
        position: relative;
        width: 100%;
        padding: 16px 0;
        border-radius: 30px;
        margin-top: 10px;
    }
}
