@import "main-bx";

.btn {
    @include fontProperty($accent_font, 600, 16px, 140%,);
    cursor: pointer;
    transition: all .2s linear;
    position: relative;
    text-transform: uppercase;

    @include below(769px) {
        font-size: 12px;
    }
    &__text {
        line-height: initial;
        transition: all .2s linear;
    }

    &__preloader {
        position: absolute;
        top: 5px;
        left: 0;
        right: 0;
        margin: auto;
        transition: all .2s linear;
        opacity: 0;
    }

    &:disabled {
        opacity: 0.7;
        cursor: no-drop;
        background: $color_orange;

        &:hover,
        &:focus,
        &:active {
            background: $color_orange;
            border-color: $color_orange;
        }

        .btn__icon,
        .btn__text {
            opacity: 0;
        }

        .btn__preloader {
            opacity: 1;
        }
    }

    &_white {
        background-color: #F9F8F7;
        color: $color_dark2;
        border: 1px solid #F9F8F7;
        &:hover {
            background-color: #F2742C;
            border-color: #F2742C;
            color: $white;
        }

        &:active {
            background-color: #F9F8F7;
            color: $color_dark2;
        }
    }

    &_orange {
        background-color: #F2742C;
        color: $white;
        border: 1px solid #F2742C;
        &:hover {
            background-color: #F9F8F7;
            color: #F2742C;
        }

        &:active {
            background-color: #F2742C;
            color: $white;
        }
    }
}