@import "main-bx";

.maintenance {
    padding: 40px 0 100px;


    @include below(769px) {
        padding: 40px 0;
    }

    &__brand {
        padding: 40px 0 60px;

        @include below(769px) {
            padding: 30px 0;
        }
    }

    &__text {
        font-size: 16px;
        line-height: 140%;
        text-align: center;
        color: #E2DAD8;

        @include below(769px) {
            font-size: 12px;
        }
    }
}