@import "main-bx";

.brand {

    display: none;

    &.active {
        display: block;
    }
    
    &__wrap {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        
        margin: -5px 0 0 -5px;
    }

    &__item {
        margin: 5px 0 0 5px;
        background: rgba(255, 255, 255, 0.95);
        border-radius: 5px;
        width: 13.85%;

        @include below(1124px) {
            width: 19%;
        }

        @include below(769px) {
            width: 31.8%;
        }

        @include below(361px) {
            width: 30%;
        }
    }

    &__box {
        position: relative;

        display: flex;
        flex-direction: column;
        align-items: center;

        padding: 32px 25px;
        background: rgba(255, 255, 255, 0.95);
        border-radius: 5px;
        cursor: pointer;
        transition: all .25s linear;
        overflow: hidden;
        &:hover {
            transform: translateY(-10px);
            background: rgba(255, 255, 255, 1);
            box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.2);

            .brand__name {
                opacity: 0;
            }

            .brand__button {
                opacity: 1;
            }
        }

        @include below(769px) {
            padding: 20px 0;
        }
    }

    &__logo {
        width: 69px;
        height: 69px;
        object-fit: contain;
        margin-bottom: 12px;

        @include below(769px) {
            margin-bottom: 5px;
            width: 49px;
            height: 49px;
        }
    }

    &__name {
        font-size: 16px;
        line-height: 140%;
        text-align: center;
        transition: all .25s linear;

        @include below(769px) {
            font-size: 14px;
        }
    }

    &__button {
        opacity: 0;
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
        transition: all .25s linear;
    }

    &__btn {
        width: 49.5%;
        padding: 13px 0;
        font-size: 10px;
        line-height: 140%;

        @include below(769px) {
            padding: 7px 0;
            font-size: 8px;
        }
    }

    &__popup {
        
        &.fancybox-content {
            width: 100%;
            max-width: 360px;
            padding: 60px 40px 50px;
            box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.3);
            border-radius: 5px;

            @include below(769px) {
                vertical-align: bottom;
                transform: translateY(21px);
            }
        }

        .fancybox-button.fancybox-close-small {
            top: 10px;
            right: 10px;
        }

        &-top {
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding-bottom: 15px;
            border-bottom: 1px solid rgba(157, 147, 144, 0.3);
        }

        &-name {
            display: block;
            font-size: 20px;
            line-height: 140%;
            margin-bottom: 10px;
        }

        &-category {
            font-size: 10px;
            line-height: 140%;
            text-transform: uppercase;
            color: #9D9390;

            background: rgba(226, 218, 216, 0.2);
            border: 1px solid #9D9390;
            padding: 4px 5px;
            border-radius: 5px;
        }

        &-logo {
            img {
                width: 69px;
                height: 69px;
            }
        }

        &-list {
            margin-top: 10px;
            margin-bottom: 32px;
            li {
                display: flex;
                align-items: center;
                justify-content: space-between;
                border-radius: 5px;
                padding: 21px 17px 20px 16px;
                &:nth-child(even) {
                    background: rgba(226, 218, 216, 0.2);
                }
            }
        }

        &-btn {
            width: 100%;
            padding: 15px;
            border-radius: 30px;
        }
    }
}