@import "main-bx";

.header {
	&__inner {
        padding: 20px 0 25px;
        background-color: #292929;

        @include below(769px) {
            padding: 14px 0;
        }
    }

    &__wrap {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__pin {
        display: none;
        position: relative;

        &-count {
            position: absolute;
            top: 0;
            right: 0;
            width: 16px;
            height: 16px;
            border-radius: 50%;
            background-color: #F2742C;
            display: flex;
            align-items: center;
            justify-content: center;

            font-weight: 600;
            font-size: 10px;
            line-height: 140%;
            color: $white;
        }

        @include below(1000px) {
            display: block;
        }
    }

    &__contact {
        display: flex;
        align-items: center;
        margin-left: -60px;

        @include below(1124px) {
            margin-left: -30px;
        }

        &-item {
            margin-left: 60px;

            @include below(1124px) {
                margin-left: 30px;
            }
        }

        @include below(1000px) {
            display: none;
        }
    }

    &__address {
        font-weight: 600;
        font-size: 12px;
        line-height: 100%;
        color: $color_grey;
        margin-bottom: 5px;
    }

    &__phone {
        display: block;
        font-size: 16px;
        line-height: 100%;
        margin-bottom: 12px;
        color: $white;
        transition: all .25s linear;
        &:hover {
            opacity: 0.7;
        }

        @include below(1320px) {
            font-size: 14px;
        }
    }

    &__anchor {
        display: flex;
        align-items: center;

        &:hover {
            .header__anchor-text {
                color: #f58647;
            }
        }

        &-pin {
            width: 11px;
            height: 11px;
            margin-right: 5px;
        }

        &-text {
            font-size: 12px;
            line-height: 100%;
            color: $color_orange;
            transition: all .25s linear;
        }
    }

    &__call {
        padding: 12px 35px;
        border-radius: 30px;

        @include below(769px) {
            padding: 9px 20px;
        }
    }
}