@import "main-bx";

.modal {
    display: none;
    width: 100%;
    
    &.fancybox-content {
        width: 100%;
        max-width: 360px;
        padding: 70px 20px 50px;
        box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.3);
        border-radius: 5px;
    }

    .fancybox-button.fancybox-close-small {
        top: 10px;
        right: 10px;
    }

    &__container {
        width: 100%;
        max-width:526px;
        margin: 0 auto;
    }

    &__title {
        text-align: center;
        margin-bottom: 40px;

        @include below(769px) {
            margin-bottom: 24px;
        }
    }

    &__text {
        @include fontProperty($accent_font, 400, 24px, 110%, $black);
        margin-bottom: 24px;

        @include below(769px) {
            font-size: 16px;
            line-height: 110%;
            margin-bottom: 16px;
        }
    }
}

.select2-container.select2-container--default.select2-container--open {
    z-index: 120000;
}